<template>
	<div class="search">
		<!--  -->
		<header class="search_header">
			<nav-bar></nav-bar>
		</header>

		<!--  -->
		<div class="search_main">
			<!-- tabs -->
			<div class="head-contain">
				<div class="search-wrap">
					<div class="s-logo">
						<!-- <img src="../assets/img/search-logo.png" alt="" /> -->
					</div>
					<div class="s-inp">
						<el-input class="search_inp" placeholder="" v-model="searchInput"
							@keyup.enter.native="searchConfirm" clearable>
						</el-input>
					</div>
					<!-- <div class="s-btn">
						<el-button class="search_btn" type="search" @click="searchConfirm">
							<i class="iconfont icon-sousuo search_logo"></i>
							<span class="search_title"></span>
						</el-button>
					</div> -->
				</div>
			</div>
			<div style="width: 1200px;margin: 0px auto;">
				<div class="body-contain" style="margin-top: 20px;width: 71%;float: left;">
					<!--  -->
					<el-card class="box-card">
						<div class="text item">
							<video :style="resolution == '16x9'? 'width: 100%;' : 'width: 32%;' " style="height: 100%;"
								:src="mainDataList.video_url" autoplay controls></video>
						</div>
					</el-card>
				</div>
				<div style="width: 27%; float: left;margin-left: 20px;margin-top: 20px;">
					<el-card class="box-card">
						<div class="text item">
							<el-tag>:</el-tag><span style="margin-left: 12px;"></span>
							<el-tag type="info">{{ mainDataList.title }}</el-tag>
						</div>
						<div class="text item" style="margin-top: 15px;">
							<el-tag>:</el-tag><span style="margin-left: 12px;"></span>
							<el-tag type="info">{{ mainDataList.theme_type == 'free' ? '' : '' }}</el-tag>
						</div>
						<div class="text item" style="margin-top: 15px;">
							<el-tag>:</el-tag><span style="margin-left: 12px;"></span>
							<el-tag type="info">{{ mainDataList.resolution }}</el-tag>
						</div>
						<div class="text item" style="margin-top: 15px;">
							<el-tag>:</el-tag><span style="margin-left: 12px;"></span>
							<el-tag type="info">{{ mainDataList.duration }}S</el-tag>
						</div>
						<div class="text item" style="margin-top: 15px;">
							<el-tag>:</el-tag><span style="margin-left: 12px;"></span>
							<el-tag type="info">{{ mainDataList.description == '' ? '' : mainDataList.description }}
							</el-tag>
						</div>
						<div class="text item" style="margin-top: 15px;">
							<el-tag>:</el-tag><span style="margin-left: 12px;"></span>
							<el-tag type="info">{{ mainDataList.is_support_hd == 1 ? '' : '' }}</el-tag>
						</div>
						<div class="text item" style="margin-top: 15px;">
							<el-tag>:</el-tag><span style="margin-left: 12px;"></span>
							<el-tag type="info">{{ mainDataList.text_dynamic == 1 ? '' : '' }}</el-tag>
						</div>
						<div class="text item" style="margin-top: 55px;">
							<el-button type="primary" round @click="offin()"></el-button>
							<el-button type="success" round><a class="a-wher" style="color: white;"
									@click="linkDownload(`http://houtai.yaojiushenjiu.com/tmpl/#/jp?theme_id=${ theme_id }&status=${ status }&uid=${uid}`)"></a>
							</el-button>
						</div>
					</el-card>
				</div>
			</div>
		</div>
		<!--  -->
		<el-backtop>
			<div class="backtop-main">
				<img src="../assets/img/backtop.png" alt="" />
			</div>
		</el-backtop>
	</div>
</template>

<script>
	import VideoItem from "./common/videoitem/VideoItem";
	// 
	import partitionData from "./partitionList";
	const Base64 = require('js-base64').Base64;
	export default {
		name: "search",
		components: {
			VideoItem,
		},
		data() {
			return {
				//   url keyword
				searchInput: this.$route.query.keyword,
				id: this.$route.query.id,
				resolution: this.$route.query.resolution,
				theme_id: this.Decrypt(this.$route.query.theme_id), //id
				uid: this.Decrypt(localStorage.getItem('user_id')),
				status: this.Decrypt('3'),
				// 
				mainDataList: [],
				mainDataListCopy: [],
				//  
				mainDataYes: true,
				// item
				currentIndex: null,
				// 
				currentTime: 0,
				// 
				currentPage: 1,
				playerOptions: {
					//
					playbackRates: [0.5, 1.0, 1.5, 2.0],
					//true,
					autoplay: true,
					// 
					muted: false,
					// 
					loop: false,
					// <video>auto,
					preload: 'auto',
					language: 'zh-CN',
					//  - "16:9""4:3"
					aspectRatio: '16:9',
					// trueVideo.js player
					fluid: true,
					sources: [{
						//
						type: "video/mp4",
						//url
						src: 'https://permanent.aoscdn.com/app/lightmv/themes/1752/1752LR.mp4'
					}],
					//
					poster: 'https://permanent.aoscdn.com/app/lightmv/themes/1752/1752.jpg?x-oss-process=style/540p',
					//Video.js
					notSupportedMessage: '',
					controlBar: {
						timeDivider: true,
						durationDisplay: true,
						remainingTimeDisplay: false,
						//
						fullscreenToggle: true
					}
				}
			};
		},
		created() {
			this.getData();
		},
		mounted() {},
		watch: {
			// 
			durationActive() {
				this.promptMessage();
			},
			partitionActive() {
				this.promptMessage();
			},
		},
		methods: {
			tabsClick() {
				console.log("tabs");
			},
			//
			offin() {
				this.$router.go(-1);
			},
			//
			sand() {

			},
			Decrypt(word) {
				const CryptoJS = require("crypto-js");
				let key = '654mca0l38b489d9f306a5b8e105334b',
					iv = 'c5defg0045222c52'
				let encrypt = CryptoJS.AES.encrypt(word, CryptoJS.enc.Utf8.parse(key), {
					iv: CryptoJS.enc.Utf8.parse(iv),
					mode: CryptoJS.mode.CBC,
					padding: CryptoJS.pad.Pkcs7
				})
				return Base64.encode(encrypt.toString());
			},
			ddsss() {
				window.opener = null;
				window.open('', '_self', '');
				window.close();
			},
			// 
			async getData() {
				let url = this.$base_url + 'Feentemplateinfo/feengetinfo';
				let uid = localStorage.getItem('user_id');
				const {
					data: res
				} = await this.$axiosbili({
					method: 'get',
					url: url,
					params: {
						uid: uid,
						id: this.id
					},
				});
				//   result 
				if (res.code === 105 || res.data === undefined) {
					this.mainDataYes = false;
					return;
				}
				this.mainDataYes = true;
				this.mainDataList = res.data;
			},
			linkDownload(url) {
				window.open(url, '_blank') // 
			},
			// 
			searchConfirm() {
				this.getData();
				// 
				let query = this.$router.history.current.query;
				let path = this.$router.history.current.path;
				// 
				let newQuery = this.$lodash.cloneDeep(query);
				// 
				newQuery.keyword = this.searchInput;
				this.$router.push({
					path,
					query: newQuery
				});
			},
			// 
			promptMessage() {
				this.$message({
					// 
					showClose: true,
					message: "!",
					type: "warning",
					// 
					duration: "1500",
				});
			},
			// 
			enter(i) {
				this.currentIndex = i;
			},
			leave() {
				this.currentIndex = null;
			},
		},
	};
</script>

<style scoped>
	.search {
		min-width: 1090px;
	}

	.search_header {
		background-image: linear-gradient(rgba(12, 73, 153, 0.7),
				rgba(163, 186, 250, 0.4));
	}

	.search_main {
		margin: 0 auto;
		/* width: 980px; */
	}

	.head-contain {
		padding-top: 40px;
	}

	.search-wrap {
		display: flex;
		width: 60%;
		margin: 0 auto;
	}

	.s-logo {
		width: 140px;
		height: 40px;
	}

	.s-logo img {
		width: 100%;
	}

	.s-inp {
		flex: 1;
		margin: 0 10px;
	}

	.s-btn {
		width: 90px;
		height: 40px;
	}

	.search_btn {
		position: relative;
		width: 100%;
		height: 100%;
		line-height: 100%;
		font-size: 16px;
		padding: 0;
	}

	.search_logo {
		position: absolute;
		left: 5px;
		top: 11px;
		font-size: 36px;
	}

	.search_title {
		margin-left: 20px;
	}

	/* tab */
	.el-tabs {
		margin-top: 20px;
	}

	/*  */
	.el-collapse {
		border: 0;
	}

	.allpartition {
		margin-top: 10px;
	}

	/*  */
	.partition-item {
		margin-right: 4px;
	}

	.main-data-item {
		margin-top: 20px;
		margin-right: 25px;
	}

	.main-data-item:nth-child(5n) {
		margin-right: 0;
	}

	.video-list-no {
		margin: 50px;
		text-align: center;
	}

	/*  */
	.item-hover {
		background: rgba(223, 223, 223, 0.3);
	}

	/*  */
	.backtop-main img {
		width: 120%;
	}
</style>
